<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">客户基本信息：</h4>
      </div>
      <div class="pull-right" style="margin-right: 10px">
        <el-button   @click="">删除</el-button>
        <el-button   @click="">保存</el-button>
      </div>
    </div>
    <div class="clearfix list-center">
      <!--<h-table></h-table>-->
      <el-col :span="12">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
          <el-form-item label="客户编号" >
            <el-input v-model="ruleForm.id" readonly v-if="ruleForm.editorId"></el-input>
            <p class="list-editor"  v-else >{{ruleForm.id}}</p>
          </el-form-item>
          <el-form-item label="名称" prop="name">
            <el-input v-model="ruleForm.name"
                      @blur="editorForm(ruleForm.editorName,'editorName')"
                      v-focus="ruleForm.editorName"
                      v-if="ruleForm.editorName">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorName,'editorName')" v-else>
              {{ruleForm.name}}
            </p>
          </el-form-item>
          <el-form-item label="统一社会信用代码" >
            <el-input v-model="ruleForm.code"
                      @blur="editorForm(ruleForm.editorCode,'editorCode')"
                      v-focus="ruleForm.editorCode"
                      v-if="ruleForm.editorCode">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorCode,'editorCode')" v-else>
              {{ruleForm.code}}
            </p>
          </el-form-item>
          <el-form-item label="办公地址" >
            <el-input v-model="ruleForm.address"
                      @blur="editorForm(ruleForm.editorAddress,'editorAddress')"
                      v-focus="ruleForm.editorAddress"
                      v-if="ruleForm.editorAddress">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorAddress,'editorAddress')" v-else>
              {{ruleForm.address}}
            </p>
          </el-form-item>
          <el-form-item label="电话" >
            <el-input v-model="ruleForm.phone"
                      @blur="editorForm(ruleForm.editorPhone,'editorPhone')"
                      v-focus="ruleForm.editorPhone"
                      v-if="ruleForm.editorPhone">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorPhone,'editorPhone')" v-else>
              {{ruleForm.phone}}
            </p>
          </el-form-item>
          <el-form-item label="开户银行" >
            <el-input v-model="ruleForm.bank"
                      @blur="editorForm(ruleForm.editorBank,'editorBank')"
                      v-focus="ruleForm.editorBank"
                      v-if="ruleForm.editorBank">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorBank,'editorBank')" v-else>
              {{ruleForm.bank}}
            </p>
          </el-form-item>
          <el-form-item label="银行账号" >
            <el-input v-model="ruleForm.account"
                      @blur="editorForm(ruleForm.editorAccount,'editorAccount')"
                      v-focus="ruleForm.editorAccount"
                      v-if="ruleForm.editorAccount">
            </el-input>
            <p class="list-editor" @click="editorForm(ruleForm.editorAccount,'editorAccount')" v-else>
              {{ruleForm.account}}
            </p>
          </el-form-item>
        </el-form>
      </el-col>
    </div>
    <div class="clearfix list-button">
      <div class="pull-left">
        <h4 class="list-button-text">备注：</h4>
      </div>
    </div>
    <div class="list-center-bottom" >
      <el-input type="textarea" :rows="8"
                placeholder="请输入内容"
                @blur="editorForm(editorRemark,'editorRemark')"
                v-focus="editorRemark"
                v-model="textarea" v-if="editorRemark"></el-input>
      <div class="list-remarks" @click="editorForm(editorRemark,'editorRemark')" v-else>
        {{textarea}}
      </div>
    </div>
  </div>
</template>
<script>
  import HTable from '@/components/layout/table/HTable'
  export default {
    name: 'chanceDetail',
    components: {
      HTable
    },
    data () {
      return {
        ruleForm: {
          index: '',
          editorId: true,
          name: '',
          editorName: true,
          code: '',
          editorCode: true,
          address: '',
          editorAddress: true,
          phone: '',
          editorPhone: true,
          bank: '',
          editorBank: true,
          account: '',
          editorAccount: true
        },
        rules: {
          name: [
            { required: true, message: '请输入名称', trigger: 'blur' }
          ]
        },
        type: 'create',
        textarea: '',
        editorRemark: true
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      editorForm (value, obj) {
        if (this.type === 'detail') {
          if (obj === 'editorRemark') {
            this.editorRemark = !value
          } else {
            this.ruleForm[obj] = !value
            if (event.type === 'blur') {
              if (obj === 'editorName') {
                if (this.ruleForm.name === '') {
                  this.ruleForm[obj] = value
                }
              }
            }
          }
        }
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      }
    },
    created () {
      this.getData(this.params)
      if (this.$route.query.type === 'detail') {
        this.type = 'detail'
        this.ruleForm = {
          id: '1',
          editorId: false,
          name: '李四',
          editorName: false,
          code: '1111',
          editorCode: false,
          address: 'xxxx公司',
          editorAddress: false,
          phone: '138-0013-8000',
          editorPhone: false,
          bank: '招商银行',
          editorBank: false,
          account: '888888888888',
          editorAccount: false
        }
        this.editorRemark = false
      }
    },
    mounted () {
    }
  }
</script>



